import 'bootstrap/dist/css/bootstrap.min.css'
import React, { useRef } from 'react'

import './App.css'
import { motion } from 'framer-motion'
import { FullPage, Slide } from 'react-full-page'



function Home(){   
    const scrollRef = useRef()
    const divOne = {
        width : '100%', height : '100%', display : 'flex', justifyContent : 'center', alignContent : 'center', 
        backgroundImage : `url("https://source.unsplash.com/random/1920x1200")`, backgroundSize : 'cover'
    }
    

    return (
        <>  
            
            <FullPage>
                <Slide>  
                    <div style={ divOne }>
                        <div style={{ width : '100%', height : '100%', display : 'flex', justifyContent : 'center', alignContent : 'center' }} className='row p-5 text-center'>
                                                        
                                <div className='col m-4'>
                                    <p class="text-uppercase fs-1 fw-bolder" style={{ textShadow : '1px 1px 1px white' }}>JG STUDIO</p>
                                    <p class="fs-3"  style={{ textShadow : '1px 1px 1px gray'}}> 다양한 웹사이트 제작과 웹앱, 모바일웹, IoT 관련 앱을 개발 합니다. </p>
                                    <div class="row g-3 align-items-center justify-content-center">
                                        
                                        <div class="col-auto w-50">
                                            <input type="search" placeholder="Search" class="form-control"/>
                                        </div>
                                        <div class="col-auto">
                                            <button class="btn btn-outline-success" type="submit">Search</button>
                                        </div>
                                    </div>                                   
                                </div>                      
                        </div>                        
                    </div>               
                </Slide>
                <Slide>                  
                    <div style={ divOne } ref={ scrollRef }>
                            <motion.div initial={{ opacity : 0}} whileInView={{ opacity : 1 }} viewport={{ root: scrollRef, once: false, amount: 'all' }}>
                            <div className='row m-5 p-5 opacity-75 align-content-center'>
                            
                                <div className='col m-4'>
                                    <motion.div whileTap={{ scale : 0.9 }} whileHover={{ scale : 1.2 }} transition={{ type : "spring", stiffness : 400, damping : 17}}>
                                    <div class="card">
                                        <img src="https://source.unsplash.com/random/300x300" class="card-img-top opacity-75" alt="..."/>
                                        <div class="card-body">
                                            <h5 class="card-title">Card title</h5>
                                            <p class="card-text">Some quick example text </p>
                                            <div align="center">
                                                <button class="btn btn-primary">Go</button>

                                            </div>                                            
                                        </div>
                                    </div>
                                    </motion.div>
                                </div>
                                <div className='col m-4'>
                                    <motion.div whileTap={{ scale : 0.9 }} whileHover={{ scale : 1.2 }} transition={{ type : "spring", stiffness : 400, damping : 17}}>
                                    <div class="card">
                                        <img src="https://source.unsplash.com/random/300x300" class="card-img-top opacity-75" alt="..."/>
                                        <div class="card-body">
                                            <h5 class="card-title">Card title</h5>
                                            <p class="card-text">Some quick example text </p>
                                            <div align="center">
                                                <button class="btn btn-primary">Go</button>
                                            </div>                                            
                                        </div>
                                    </div>
                                    </motion.div>
                                </div>
                                <div className='col m-4'>
                                <   motion.div whileTap={{ scale : 0.9 }} whileHover={{ scale : 1.2 }} transition={{ type : "spring", stiffness : 400, damping : 17}}>
                                    <div class="card">
                                        <img src="https://source.unsplash.com/random/300x300" class="card-img-top opacity-75" alt="..."/>
                                        <div class="card-body">
                                            <h5 class="card-title">Card title</h5>
                                            <p class="card-text">Some quick example text </p>
                                            <div align="center">
                                                <button class="btn btn-primary">Go</button>
                                            </div>                                            
                                        </div>
                                    </div>
                                    </motion.div>
                                </div>
                            </div>
                            </motion.div>
                    </div>                  
                </Slide>                
            </FullPage>
               
            
            
        </>        
    )
}
export default Home