import { useState, useEffect } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import swal from 'sweetalert'
import axios from 'axios'


function NoticeDetail(){
    const { id } = useParams()
    
    const navigate = useNavigate()
    const [values, setValues] = useState({
        id : '',
        title : '', 
        content : '', 
        hits : '', 
        image : '', 
        createAt : ''      
    })
    const [imagePreview, setImagePreview] = useState('')    

    const handleInput = (e) => {
        //e.preventDefault()
        
        setValues({...values, [e.target.name] : e.target.value})
    }
    const handleImage = (e) => {        
        //e.preventDefault()
        if(e.target.files[0]){ 
            setValues({...values, [e.target.name] : e.target.files[0]})           
            setImagePreview(e.target.files[0])
            
            var fileReader = new FileReader()
            fileReader.readAsDataURL(e.target.files[0])
            fileReader.onloadend = (ev) => {
                setImagePreview(ev.target.result)                      
            }       
            //let val = URL.createObjectURL(e.target.files[0])
            //setImagePreview(val)                     
        }
        
    }

    useEffect(() => {
        axios.get('http://localhost:8000/notice/detail/' + id)
        .then((result) => {
            setValues({ id : result.data._id, title : result.data.title, content : result.data.content, image : result.data.imageURL, createAt : result.data.createAt, hits : result.data.hits })
            if(result.data.imageURL === ''){
                setImagePreview('')
            }else{
                setImagePreview('http://localhost:8000/images/' + result.data.imageURL)
            }
            
        }).catch((err) => {
            swal('error: ' + err.response)
        })
    }, [])

    const submit = () => {
        //e.preventDefault()

        if(values.title === ''){
            swal('제목을 입력해 주세요')
            return
        }
        if(values.content === ''){
            swal('내용을 입력해 주세요')
            return
        }
        const form = new FormData()
        form.append('id', values.id)
        form.append('title', values.title)
        form.append('content', values.content)
        //form.append('createAt', new Date().getTime())
        form.append('image', values.image)

        axios({            
            url : 'http://localhost:8000/notice', 
            method : 'PUT',  
            data : form
           // cache : false        
        }).then(result => {
            console.log(result.data)
            if(result.data.message === 'SUCCESS'){
                navigate(-1)
            }

        }).catch(error => {
            swal(error.response)
        })
        
    }
    const remove = () => {
        axios({
            url : 'http://localhost:8000/notice/' + values.id, 
            method : 'DELETE'
        }).then((result) => {
            if(result.data.message === 'SUCCESS'){
                navigate('/notice')
            }
        }).catch((err) => {
            swal('error: ' + err)
        })
    }
    return (
        <>
            <br/><br/>
            <div className="container">
                <br/>
                <div className="border rounded-4">
                    <br/>   
                          
                    <div className="form-group">                        
                        <h4 className="text-center m1-20 my-3 link-offset-3"><u>공지사항</u></h4>                        
                        <br/>
                        <div className="row justify-content-end m-3 p-1">
                            <div className='col-sm-auto p-3'>
                                <h7>조회수: { values.hits }</h7>
                            </div>
                            <div className='col-sm-auto p-3'>
                             <h7>작성일: { new Date(parseInt(values.createAt)).toLocaleString() }</h7>
                            </div>                            
                        </div>
                        <label className="text-center form-label mx-3"> 제 목</label>
                        &nbsp;&nbsp;<input id="ti" type="text" className="form-control" name="title" value={values.title} onChange={ handleInput } placeholder="제목을 입력해 주세요"/>
                    </div>
                    <div className="form-group">
                        <label className="form-label mx-3">내 용</label>
                        &nbsp;&nbsp;<textarea id="cont" rows="10" className="form-control" name="content" value={values.content} onChange={ handleInput } placeholder="내용을 입력해 주세요"></textarea>
                    </div>
                    <div className="mb-3">
                        <label className="form-label">이미지</label>
                        <input className="form-control" type="file" name="image" onChange={ handleImage } accept="image/*"/>
                        {
                            imagePreview === '' ? '' : <img src={ imagePreview } width="200vw" alt="images"></img>
                        }                        
                    </div>
                    <hr className="my-2"/>
                    <div className="row justify-content-end">
                        <div className="col-sm-auto p-3">
                        <button type="button" className="btn btn-outline-info" onClick={() => {navigate(-1)}}>목록</button>
                        <button type="button" className="btn btn-outline-danger" onClick={ remove }>삭제하기</button>
                        <button type="button" id="send" className="btn btn-outline-primary" onClick={ submit }>수정하기</button>
                        </div>
                        <br/>
                    </div>   
                      
                </div>
            </div>
        </>
    )
}
export default NoticeDetail
