
import './App.css'
import { Nav, Navbar, Container } from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css'

import { Route, Routes, useNavigate } from 'react-router-dom'

import Home from './Home'
import Solution from './Solution'
import About from './About'
import NoticeList from './notice/NoticeList'
import NoticeWrite from './notice/NoticeWrite'
import NoticeDetail from './notice/NoticeDetail'


function App() {
  let navigate = useNavigate();
  return (
    <>
    
      <Navbar expand="md" fixed="top" className="bg-body-tertiary opacity-75" bg="dark" data-bs-theme="dark">
        <Container>
          <Navbar.Brand>JG STUDIO</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link style={{ cursor : 'pointer'}} onClick={ () => { navigate('/')}}>Home</Nav.Link>
              <Nav.Link style={{ cursor : 'pointer'}} onClick={ () => { navigate('/solution')}}>Solution</Nav.Link>
              <Nav.Link style={{ cursor : 'pointer'}} onClick={ () => { navigate('/about')}}>About</Nav.Link>
              {/* <Nav.Link style={{ cursor : 'pointer'}} onClick={ () => { navigate('/notice')}}>공지사항</Nav.Link>               */}
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
      
      <Routes>
        <Route path={process.env.PUBLIC_URL + "/"} element={ <Home/> }/>
        <Route path={process.env.PUBLIC_URL + "/solution"} element={ <Solution/> }/>
        <Route path={process.env.PUBLIC_URL + "/about"} element={ <About/> }/>
        <Route path={process.env.PUBLIC_URL + "/notice"} element={ <NoticeList/> }/>
        <Route path={process.env.PUBLIC_URL + "/noticeWrite"} element={ <NoticeWrite/> }/>
        <Route path={process.env.PUBLIC_URL + "/noticeDetail/:id"} element={ <NoticeDetail/> }/>
      </Routes>
  
  </> 
  );
}

export default App;
