
import { Animator, ScrollContainer, ScrollPage, batch, Fade, FadeIn, FadeOut, Move, MoveIn, MoveOut, Sticky, StickyIn, StickyOut, Zoom, ZoomIn, ZoomOut } from "react-scroll-motion";
import { Card, Image, Button, Container, Row, Col } from 'react-bootstrap'
function Solution(){
    const ZoomInScrollOut = batch(StickyIn(), FadeIn(), ZoomIn());
    const FadeUp = batch(Fade(), Move(), Sticky()); 
    const divOne = {
        justifyContent : 'center', backgroundAttachment : 'fixed', 
        alignContent : 'center',  position : 'sticky', top : 0,   
        backgroundImage : `url("https://source.unsplash.com/random/1920x1200")`, backgroundSize : 'cover'
    }

    

    return (
        <>
            <div style={ divOne }>
                <ScrollContainer>
                    <ScrollPage>
                        <Animator animation={batch(Fade(), Sticky(), MoveOut(0, -200))}>
                        <Container fluid="true">
                        <Row className="justify-content-center">
                            <Col sm>
                                <Card className='p-3 m-3 ' style={{ width: '16rem' }}>
                                    <Card.Body>
                                        <Image roundedCircle width="150px" height="150px" src="https://source.unsplash.com/random/200x200"/>
                                        <Card.Title>Card Title</Card.Title>
                                        <Card.Text>
                                        Some quick example text to build on the card title and make up the
                                        bulk of the card's content.
                                        </Card.Text>
                                        <Button variant="primary">Go somewhere</Button>
                                    </Card.Body>
                                </Card>
                            </Col>
                            <Col sm>
                                <Card className='p-3 m-3 ' style={{ width: '16rem' }}>
                                    <Card.Body>
                                        <Image roundedCircle width="150px" height="150px" src="https://source.unsplash.com/random/200x200"/>
                                        <Card.Title>Card Title</Card.Title>
                                        <Card.Text>
                                        Some quick example text to build on the card title and make up the
                                        bulk of the card's content.
                                        </Card.Text>
                                        <Button variant="primary">Go somewhere</Button>
                                    </Card.Body>
                                </Card>
                            </Col>
                            <Col am>
                                <Card className='p-3 m-3 ' style={{ width: '16rem' }}>
                                    <Card.Body>
                                        <Image roundedCircle width="150px" height="150px" src="https://source.unsplash.com/random/200x200"/>
                                        <Card.Title>Card Title</Card.Title>
                                        <Card.Text>
                                        Some quick example text to build on the card title and make up the
                                        bulk of the card's content.
                                        </Card.Text>
                                        <Button variant="primary">Go somewhere</Button>
                                    </Card.Body>
                                </Card>
                            </Col>
                        </Row>
                        </Container>
                        <span style={{ fontSize: "30px" }}>Let me show you scroll animation 😀</span>
                        </Animator>
                    </ScrollPage>
                    <ScrollPage>
                        <Animator animation={ZoomInScrollOut}>
                        <span style={{ fontSize: "40px" }}>I'm FadeUpScrollOut ✨</span>
                        </Animator>
                    </ScrollPage>
                    <ScrollPage>
                        <Animator animation={FadeUp}>
                        <span style={{ fontSize: "40px" }}>I'm FadeUp ⛅️</span>
                        </Animator>
                    </ScrollPage>
                    <ScrollPage>
                        <div style={{ display: "flex", justifyContent: "center", alignItems: "center", height: "100%" }} >
                        <span style={{ fontSize: "40px" }}>
                            <Animator animation={MoveIn(-1000, 0)}>Hello Guys 👋🏻</Animator>
                            <Animator animation={MoveIn(1000, 0)}>Nice to meet you 🙋🏻‍♀️</Animator>
                            - I'm Dante Chun -
                            <Animator animation={MoveOut(1000, 0)}>Good bye ✋🏻</Animator>
                            <Animator animation={MoveOut(-1000, 0)}>See you 💛</Animator>
                        </span>
                        </div>
                    </ScrollPage>
                    <ScrollPage>
                        <Animator animation={batch(Fade(), Sticky())}>
                        <span style={{ fontSize: "40px" }}>Done</span>
                        <br/>
                        <span style={{ fontSize: "30px" }}>
                            There's FadeAnimation, MoveAnimation, StickyAnimation, ZoomAnimation
                        </span>
                        </Animator>
                    </ScrollPage>
                </ScrollContainer>
            </div>
        </>
    )
}

export default Solution