import { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import swal from 'sweetalert'
import axios from 'axios'


function NoticeWrite(){
    const navigate = useNavigate()
    const [values, setValues] = useState({
        title : '', 
        content : '', 
        image : ''        
    })
    const [imagePreview, setImagePreview] = useState('')

    

    const handleInput = (e) => {
        //e.preventDefault()
        
        setValues({...values, [e.target.name] : e.target.value})
    }
    const handleImage = (e) => {        
        //e.preventDefault()
        if(e.target.files[0]){ 
            setValues({...values, [e.target.name] : e.target.files[0]})           
            setImagePreview(e.target.files[0])
            
            var fileReader = new FileReader()
            fileReader.readAsDataURL(e.target.files[0])
            fileReader.onloadend = (ev) => {
                setImagePreview(ev.target.result)                      
            }       
            //let val = URL.createObjectURL(e.target.files[0])
            //setImagePreview(val)                     
        }
        
    }
    const submit = () => {
        //e.preventDefault()

        if(values.title === ''){
            swal('제목을 입력해 주세요')
            return
        }
        if(values.content === ''){
            swal('내용을 입력해 주세요')
            return
        }
        const form = new FormData()
        form.append('title', values.title)
        form.append('content', values.content)
        form.append('createAt', new Date().getTime())
        form.append('image', values.image)

        axios({
            // headers : {
            //              "Content-Type": "multipart/form-data",                          
            //              "Access-Control-Allow-Origin": "*"                         
            //         }, 
            url : 'http://localhost:8000/notice', 
            method : 'POST',  
            data : form
           // cache : false        
        }).then(result => {
            console.log(result.data)
            if(result.data.message === 'SUCCESS'){
                navigate('/notice')
            }

        }).catch(error => {
            swal(error.response)
        })
        
    }
    return (
        <>
            <br/><br/>
            <div className="container">
                <br/>
                <div className="border rounded-4">
                    <br/>   
                          
                    <div className="form-group">
                        <h4 className="text-center m1-20 my-3 link-offset-3"><u>공지사항 글쓰기</u></h4>                        
                        <br/>
                        <label className="text-center form-label mx-3"> 제 목</label>
                        &nbsp;&nbsp;<input id="ti" type="text" className="form-control" name="title" value={values.title} onChange={ handleInput } placeholder="제목을 입력해 주세요"/>
                    </div>
                    <div className="form-group">
                        <label className="form-label mx-3">내 용</label>
                        &nbsp;&nbsp;<textarea id="cont" rows="10" className="form-control" name="content" value={values.content} onChange={ handleInput } placeholder="내용을 입력해 주세요"></textarea>
                    </div>
                    <div className="mb-3">
                        <label className="form-label">이미지</label>
                        <input className="form-control" type="file" name="image" onChange={ handleImage } accept="image/*"/>
                        {
                            imagePreview === '' ? '' : <img src={ imagePreview } width="200vw" alt="images"></img>
                        }                        
                    </div>
                    <hr className="my-2"/>
                    <div className="row">
                        <div className="col-md-3 offset-md-10">
                        <button type="button" className="btn btn-outline-danger" onClick={() => {navigate('/notice')}}>취소</button>
                        <button type="button" id="send" className="btn btn-outline-primary" onClick={ submit }>글쓰기</button>
                        </div>
                        <br/>
                    </div>   
                      
                </div>
            </div>
        </>
    )
}

export default NoticeWrite
