import { useEffect, useState } from 'react'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'
import Pagination from 'react-js-pagination'

import '../App.css'

function NoticeList(){    
    
    const [noticeList, setNoticeList] = useState([])
    const [activePage, setActivePage] = useState(1)
    const [totalCount, setTotalCount] = useState(0)
    const navigate = useNavigate();


    const getNoticeList = () => {        
            axios.get('http://localhost:8000/notice/' + activePage)
            .then((result) => {
                console.log(result)
                setTotalCount(result.data.totalCount)
                setNoticeList(result.data.list)
            }).catch((err) => {

            })
            
       
    }

    useEffect(() => {
        getNoticeList()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [activePage])
    
    const handlePageChange = (pageNumber) => {
        console.log('pageNumber: ' + pageNumber)
       setActivePage( pageNumber)
    }

    return ( 
        <>   
            <br/>
            <div className="container my-5 p-2 border rounded-3">
                <div align="center">
                    <h4 className="text-center m1-20 my-3 link-offset-3"><u>공 지 사 항</u></h4>                        
                </div>
                <div className="my-2" align="right">
                    <button type="button" class="btn btn-outline-primary" onClick={() => { navigate('/noticeWrite') }}>글쓰기</button>        
                </div>
                <div className="row font-weight-bold py-2 bg-secondary bg-opacity-25">
                    <div className="col text-center">제 목</div>
                    <div className="col text-center">올린날자</div>
                    <div className="col text-center">조회수</div>
                    <div className="col text-center">사진파일</div>
                </div>        
                {
                    noticeList.map((notice, i) => {                        
                        return  <div key={i} className={(i % 2 === 0) ? "row py-2" : "row py-2 bg-secondary bg-opacity-10"} >
                                    <div className="col" style={{ cursor : 'pointer' }} onClick={() => { navigate('/noticeDetail/' + notice._id) }}>{ notice.title }</div>
                                    <div className="col text-center">{ new Date(parseInt(notice.createAt)).toLocaleString() }</div>
                                    <div className="col text-center">{ notice.hits }</div>
                                    <div className="col text-center">
                                        {
                                            notice.imageURL === '' ? '' : <img width="20vw" height="20vh" src={ 'http://localhost:8000/images/' + notice.imageURL } alt="images"/>
                                        }
                                                                          
                                    </div>
                                </div>
                                
                    })                     
                }               
            </div>
            <div align="center">
                <Pagination activePage={activePage} itemsCountPerPage={10} totalItemsCount={totalCount} 
                            pageRangeDisplayed={10}  onChange={handlePageChange} />
            </div>
        </>      
    )
}





export default NoticeList
